body
{
    font-family: 'Roboto', sans-serif;
    color: var(--black);
}

:root {
    --black: #373737;
    --danger: #FF0000;
    --light-danger: #FFCECE;
    --orange: #ff5b01;
    --light-orange: #FFB088;
    --light-light-orange: #FFE9DE;
    --light-grey: #D0D0D0;
    --dark-grey: #807e7e;
    --grey: #999999;
    --green: #29CD1E;
    --success: #23C76F;
    --light-success: #D1FFCE;
    --blue: #3e83d1;
    --blue-trans: rgba(62, 131, 209, 0.76);
    --light-blue:#5fb8ff;
  }

  .btn-primary
  {
    background-color: var(--blue);
    border: white; 
  }
  .btn-primary:hover
  {
    background-color: var(--light-blue);
  }

  .text-blue{
      color: var(--blue);
  }
  .text-light-blue{
    color: var(--light-blue);
    }
  .text-grey{
    color: var(--grey);
    }
    .text-light-grey{
        color: var(--light-grey);
        }
 

  .pointer
  {
      cursor: pointer;
  }